
import Vue from 'vue';
import VueI18n from 'vue-i18n';
import en from './en';
import vi from './vi';

Vue.use(VueI18n);

const locale = 'en';

const messages = {
    en: en,
    vi: vi
};

const i18n = new VueI18n({
    locale,
    messages
});

export {
    locale,
    i18n
};



