
import Vue from 'vue';

import { i18n } from './resume/lang';
import store from './resume/store/index';
import Resume from './resume/resume.vue';

Vue.config.devtools = process.env.NODE_ENV !== 'production' ? true : false;

export const app =  new Vue({
    store,
    i18n,
    el: '#vc--resume',
    render: h => h(Resume)
});




