
import * as types from '../mutation-types';
import * as actions from './actions';
import * as getters from './getters';

const state = {
    fetching: false,
    profile: null,
    page: 1,
    print: false
};

const mutations = {
    [types.BEFORE_GET_RESUME] (state) {
        state.fetching = true;
    },
    [types.GET_RESUME] (state, payload) {
        state.profile  = payload.resume || {};
        state.fetching = false;
    },
    [types.SET_RESUME_PAGE] (state, payload) {
        state.page  = parseInt(payload.page);
    },
    [types.SET_PRINT] (state, payload) {
        state.print  = payload.print;
    }
};

export default {
    //namespaced: true,
    state,
    actions,
    getters,
    mutations
};


